<template>
  <section class="shop-list">
    <section class="shop-list-item" v-for="n in list" :key="n.id">
        <router-link to="/shop">
        <div class="top-item">
            <div class="logo-wrap">
                <img :src="n.pic" alt="">
            </div>
            <div class="list-main">
                <div class="title">
                    <h3>{{n.title}}</h3>                
                </div>
                <div class="star">
                    <span>
                        <el-rate
                        v-model="n.star"
                        disabled
                        show-score
                        text-color="#ff9900"
                        score-template="{value}">
                        </el-rate>
                    </span><span>月售{{n.outnumber}}单</span>
                </div>
                <div class="price">
                    <span>{{n.minnumber}}起送</span><span>配送费{{n.boynumber}}元</span>
                </div>
            </div>
        </div>
        </router-link>
        <div class="bottom-item">
            <div class="bottom-item-top">
                <img src="static/elm/img/d.png" alt="">
                <span>口碑人气好店</span>
            </div>
            <div class="bottom-item-bottom" v-bind:class="{ 'bottom-item-bottom-click': n.isActive}">
                <div class="item-number" @click.stop="n.isActive = !n.isActive">
                    共5个活动 <span><i class="fas fa-caret-down"></i></span>
                </div>
                <div class="bottom-item-item">
                    <span class="green red">首</span><span>{{n.title}}</span>
                </div>
                <div class="bottom-item-item">
                    <span class="red">折</span><span>{{n.title}}</span>
                </div>
                <div class="bottom-item-item">
                    <span class="red">减</span><span>{{n.title}}</span>
                </div>
                <div class="bottom-item-item">
                    <span class="red">特</span><span>{{n.title}}</span>
                </div>
                <div class="bottom-item-item">
                    <span class="red">折</span><span>{{n.title}}</span>
                </div>
                <div class="bottom-item-item">
                    <span class="red">减</span><span>{{n.title}}</span>
                </div>
                <div class="bottom-item-item">
                    <span class="red">特</span><span>{{n.title}}</span>
                </div>
            </div>
        </div>
    </section>
  </section>
</template>
<script>
export default {
  props:{
    list:Array,
  },
  data: function () {
    return {
      show: false,
      isfixed: false,
      isActive:false
    }
  },
  methods: {
    more: function () {
      n.isActive = !n.isActive
    }
  }
}
</script>

<style scoped>
.shop-list{
    width: 100vw;
    border-bottom: 1px solid rgb(221, 219, 219);
    background-color: white;
}
.shop-list-item{
    padding: 3vw 0;
    border-bottom:.133333vw solid #eee;
    color: #666;
}
.shop-list-item a{
    color: #666;
}
.top-item{
    display: flex;
    width: 100vw;
    padding: 0 2.5vw;
    color: #666;
}
.logo-wrap{
    position: relative;
    width: 18vw;
    height: 18vw;
    border: 1px solid rgb(221, 219, 219);
}
.logo-wrap img{
    display: block;
    width: 100%;
    height: 100%;
}
.list-main{
    display: flex;
    flex-direction:column;
    justify-content:space-between;
    overflow:hidden;
    width: 77vw;
    padding-left: 2vw;
}
.title{
    display: flex;
    flex-direction:column;
    width: 100%;
    height: 5vw;
    font-size: 3.5vw;
    font-weight: 700;
    color: #333;
}
.title h3{
    margin: 0;
}
.star{
    font-size: 3.2vw;
}
.el-rate{
    display: inline-block;
    height: 3.2vw;
    padding-right: 3vw;
}

.price {
    font-size: 3.2vw;
    line-height: 3.2vw;
}
.price span:not(:nth-of-type(1))::before{
  display: inline-block;
  content: '|';
  margin: 0 .8vw;
}
.bottom-item{
    width: 100vw;
    padding: 3vw 2.5vw 0vw 22.5vw;
}
.bottom-item-top{
    width:100%; 
    height: 5vw;
    color:rgb(232, 71, 11);
    font-size: 2.8vw;
    padding-bottom: 2.2vw;
    border-bottom: .133333vw solid #eee;
}
.bottom-item-top img{
    height: 2.8vw;
    float: left;
    margin: auto 0;
    vertical-align:center;
}
.bottom-item-bottom{
    position: relative;
    width: 100%;
    height: 12vw;
    overflow: hidden;
    margin-top: 2vw;
}
.bottom-item-bottom-click{
    position: relative;
    width: 100%;
    height: auto;
    margin-top: 2vw;
}
.item-number{
    position:absolute;
    right: 0;
    top: 0;
    height: 5vw;
    font-size: 3.2vw;
    line-height: 5vw;
}
.bottom-item-item{
    width: 100%;
    height: 5vw;
    font-size: 3.2vw;
    line-height: 5vw;
    margin-top: 1vw;
}
.bottom-item-item:first-child{
    margin-top: 0;
}
.bottom-item-item .red{
    display: inline-block;
    width: 4vw;
    font-size: 3vw;
    margin-right: 2vw;
    text-align: center;
    color: white;
    border-radius: 1vw;
    background-color: rgb(240, 115, 115);
}
.bottom-item-item .green{
    background-color: rgb(112, 184, 98);
}
.bottom-item-item span{
    display: inline-block;
}
</style>



